import { h, Component } from 'preact';
import { rvec2, rvec3, rvec4 } from '../math';
import { vec3 } from 'gl-matrix';
import './vector-view.scss';
import NumberInput from './number-input';

type basicVector = rvec2 | rvec3 | rvec4;
type ForceLength = undefined | 2 | 3 | 4;
interface Props
{
    readonly disabled?: boolean;
    readonly value: basicVector;
    readonly forceLength?: ForceLength;
    readonly onChange: (oldVector: basicVector, newVector: basicVector) => void;
}

function isVec3(x: basicVector, forceLength: ForceLength): x is rvec3
{
    return x.length >= 3 || forceLength >= 3;
}

function isVec4(x: basicVector, forceLength: ForceLength): x is rvec4
{
    return x.length === 4 || forceLength === 4;
}

export default class VectorView extends Component<Props>
{
    public render()
    {
        const { value: v, disabled, forceLength } = this.props;
        const isV3 = isVec3(v, forceLength);
        const isV4 = isVec4(v, forceLength);

        return <div class='vector control-group'>
            <NumberInput class='input' disabled={disabled} value={v[0]} onChange={this.onChangeX} placeholder='x' step={0.1} />
            <NumberInput class='input' disabled={disabled} value={v[1]} onChange={this.onChangeY} placeholder='y' step={0.1} />

            { (isV3) &&
            <NumberInput class='input' disabled={disabled} value={v[2]} onChange={this.onChangeZ} placeholder='z' step={0.1} />
            }
            { isV4 &&
            <NumberInput class='input' disabled={disabled} value={v[3]} onChange={this.onChangeW} placeholder='w' step={0.1} />
            }
        </div>;
    }

    private onChangeX = (oldValue: number, newValue: number) =>
    {
        this.updateVector(newValue, 0);
    }

    private onChangeY = (oldValue: number, newValue: number) =>
    {
        this.updateVector(newValue, 1);
    }

    private onChangeZ = (oldValue: number, newValue: number) =>
    {
        this.updateVector(newValue, 2);
    }

    private onChangeW = (oldValue: number, newValue: number) =>
    {
        this.updateVector(newValue, 3);
    }

    private updateVector = (value: number, index: number) =>
    {
        const v = this.props.value;

        console.log('Update vector index', index, value);

        const newVector = vec3.clone(v);
        newVector[index] = value;
        this.props.onChange(v, newVector);
    }
}